.footer {
    color: #fff;
    background-color: #000;
    padding: 3rem 2rem 5rem 2rem;
}

.footer__tm_nav {
    display: flex;
    flex-direction: row;
    gap: 20%;
}

.footer__tm_nav .trademark_info {
    width: 25%;
    font-size: clamp(0.8rem, 2vw, 1rem);
}
@media screen and (max-width: 600px) {
    .footer__tm_nav {
        flex-direction: column;
    }
    .trademark_info {
        width: 100% !important;
    }
}